import React, { useState, useRef, useEffect } from 'react';
import {Route, Switch, Redirect , useHistory, useLocation } from 'react-router-dom';
import { AnimatePresence, motion } from "framer-motion";
import Header from "./components/header";
import Main from "./components/Main/index";
import Work from "./components/Work/index";
import History from "./components/History/index"

const App = () => {
  const location = useLocation();
  const [isMainNav, setMainNav] = useState(false);
  const [isMain, setMain] = useState(true);
  const [isSub, setSub] = useState(false);
  const [isWork, setWork] = useState(false);
  const [isHistory, setHistory] = useState(false);
  const video = useRef(null);
	const wave = useRef(null);
  let isPlaying = false;

  const headerSub = () => {
    setMain(false);
    setSub(true);
    setMainNav(true);
  }
  const mainState = () =>{
    setMain(true);
    video.current.load();
    videoPlay();
  }

  const workState = (e) => {
    setWork(e);
  }
  const historyState = (e) =>{
    setHistory(e);
  }

	const videoPlay = (e) => {
		if(isPlaying){
			return;
    }
		setMainNav(false);
		video.current.play();
		isPlaying = true;

		video.current.addEventListener('timeupdate', (e) => {
      if(!wave.current){
        return;
      }
			let currentTime = e.target.currentTime;
			if(e.target.ended){
        isPlaying = false;
        //e.target.load();
      }
      //console.log(currentTime);
			if(currentTime > 6){
        setMainNav(true);
				return;
			}
			wave.current.style.transform = 'translateY(-' + (currentTime * 50) +'px)';
			//console.log(e.target.currentTime);
    });
  }

  const history = useHistory();

  useEffect(() => {
    return history.listen((location) => {
      if(location.pathname === '/'){
        setMainNav(false);
        setSub(false);
        setWork(false);

      } else {
        setMainNav(true);
        setSub(true);
        setWork(false);
        setHistory(false);
      }
    })
  },[history])

	return (
    <>
      <Header 
        isMainNav={isMainNav} 
        isMain={isMain} 
        isSub={isSub} 
        headerSub={headerSub}
        isWork={isWork}
        isHistory={isHistory}
      /> 
      <AnimatePresence>
        <Switch location={location} key={location.pathname}>
          <Route exact path="/" render={() => 
            <Main 
              isMainNav={isMainNav} 
              videoEl={video}
              videoPlay={videoPlay} 
              // videoStop={videoStop}
              mainState={mainState}
              isMain={isMain} 
              wave={wave} 
              isWork={isWork}
              />} 
            />
          <Route exact path="/work" render={() => <Work headerSub={headerSub} />} />
          <Route exact path="/history" render={() => <History headerSub={headerSub} />} />
          <Redirect path="*" to="/" />
        </Switch>
      </AnimatePresence>
    </>
	);
}

export default App;